<template>
  <!-- 主体部分组件 -->
  <main id="main" align="center">
    <!-- 中间博客内容 -->
    <div class="main-1" id="main-1">
      <page-one />
    </div>
    <!-- 右侧个人简介 -->
    <div id="my" class="my">
      <Profile />
    </div>
  </main>
</template>
<script>
import PageOne from "../pageOne/PageOne";
import Profile from "../profile/Profile";
export default {
  components: {
    PageOne,
    Profile,
  },
  methods: {
    //获取main的高度确定底部
    // getHeight() {
    //   var mainHeight =
    //     document.getElementsByClassName("main-1")[0].clientHeight + 100;
    //   document.getElementById("main").style.height = mainHeight;
    // },
    //让个人资料模块滚动到顶部固定
    runD() {
      var myheight = document.getElementById("my").offsetTop - 45;
      window.addEventListener("scroll", function (event) {
        var scrollTop =
          document.documentElement.scrollTop ||
          window.pageYOffset ||
          document.body.scrollTop;
        if (scrollTop > myheight) {
          document.getElementById("my").classList.add("myOne");
        } else {
          document.getElementById("my").classList.remove("myOne");
        }
      });
    },
  },
  mounted() {
    // this.getHeight();
    // this.runD();
  },
};
</script>
<style scoped>
@import "./Main";
</style>


