<template>
  <div>
    <!--  头部导航栏组件-->
    <header
      style="
        background-image: url(https://lymblog.oss-cn-shanghai.aliyuncs.com/images/backgroundImage2.png);
      "
    >
      <Nav1 />
      <!-- 中间的字 -->
      <div class="conten-font">
        <div class="contenText">
          {{ this.$store.getters.getUserData.blogTitle }}
        </div>
        <div id="text">
          <vue-typer
            :text="this.$store.getters.getUserData.blogSaying"
            :repeat="Infinity"
            :shuffle="false"
            initial-action="erasing"
            :pre-type-delay="70"
            :type-delay="70"
            :pre-erase-delay="2000"
            :erase-delay="100"
            erase-style="backspace"
            :erase-on-complete="true"
            caret-animation="blink"
          ></vue-typer>
        </div>
      </div>
    </header>
  </div>
</template>
<script>
import Vue from "vue";
import Nav1 from "../nav/Nav";
import { VueTyper } from "vue-typer"; //自动打字效果
export default {
  name: "HelloWorld",
  data() {
    return {};
  },
  methods: {
  },
  mounted() {
  },
  components: {
    Nav1,
    VueTyper,
  },
};
</script>
<style scoped>
@import "../header/Header.css";
</style>


