<template>
  <!-- 博客首页内容 -->
  <div>
    <ul v-for="(date, index) in this.$store.getters.getAllBlog" :key="index">
      <li v-if="index % 2 == 0">
        <div class="li-div" @click="getDesc(date.id)">
          <!-- 博客图片 -->
          <div class="img" style="border-radius: 10px 0px 0px 10px">
            <img
              :src="date.img"
              alt=""
              style="border-radius: 10px 0px 0px 10px"
            />
          </div>
          <!-- 博客简介内容 -->
          <div class="conent" style="border-radius: 0px 10px 10px 0px">
            <!-- 标题 -->
            <h2 class="conent-title">{{ date.title }}</h2>
            <!-- 分类详情 -->
            <div class="conent-div1">
              <Icon type="md-time" /> 发表于 {{ date.createTime }} |
              <Icon type="ios-filing-outline" />
              <a
                :href="date.fileUrl"
                target="_blank"
                v-if="date.fileName != ''"
                >{{ date.fileName }}</a
              >
              <a v-else>-</a>
              |
              <Icon type="md-pricetag" /> <a href="#">{{ date.label }}</a>
            </div>
            <!-- 博客简介 -->
            <div class="desc-1">
              {{
                date.desc.length > 118
                  ? date.desc.slice(0, 118) + "......"
                  : date.desc
              }}
            </div>
          </div>
        </div>
      </li>
      <li v-else>
        <div class="li-div" @click="getDesc(date.id)">
          <!-- 博客简介内容 -->
          <div class="conent" style="border-radius: 10px 0px 0px 10px">
            <!-- 标题 -->
            <h2 class="conent-title">{{ date.title }}</h2>
            <!-- 分类详情 -->
            <div class="conent-div1">
              <Icon type="md-time" /> 发表于 {{ date.createTime }} |
              <Icon type="ios-filing-outline" />
              <a
                :href="date.fileUrl"
                target="_blank"
                v-if="date.fileName != ''"
                >{{ date.fileName }}</a
              >
              <a v-else>-</a>
              |
              <Icon type="md-pricetag" /> <a href="#">{{ date.label }}</a>
            </div>
            <!-- 博客简介 -->
            <div class="desc-1">
              {{
                date.desc.length > 118
                  ? date.desc.slice(0, 118) + "......"
                  : date.desc
              }}
            </div>
          </div>
          <!-- 博客图片 -->
          <div
            class="img"
            style="border-radius: 0px 10px 10px 0px; float: right"
          >
            <img
              :src="date.img"
              alt=""
              style="border-radius: 0px 10px 10px 0px"
            />
          </div>
        </div>
      </li>
    </ul>
    <Page
      :total="this.$store.getters.getAllBlog.length"
      show-elevator
      :page-size="6"
    />
  </div>
</template>
<script>
export default {
  methods: {
    //点击li跳转desc详情页
    getDesc(id) {
      this.$router.push({
        path: `/desc`,
        query: {
          id: id,
        },
      });
    },
  },
};
</script>
<style scoped>
@import "../main/Main";
</style>


