<template>
  <ul>
    <!-- 个人资料 -->
    <li>
      <div class="myTitle">
        <h3>个人资料</h3>
        <br />
        <Avatar
          class="myTitle-avatar"
          :src="this.$store.getters.getUserData.portrait"
          size="100"
        />
        <!-- 展示个人用户名和签名 -->
        <div class="user">
          <span class="userName">
            {{ this.$store.getters.getUserData.userName }} </span
          ><br />
          <span class="userAutograph">
            {{ this.$store.getters.getUserData.autograph }}
          </span>
        </div>
        <!-- 展示文章 标签 阅读量 -->
        <div class="blogNum">
          <ul>
            <li>
              <!-- 文章数量 -->
              <div class="WZ">
                <h3>文章</h3>
                <div class="xian">————</div>
                <div class="wzNum">
                  {{ this.$store.getters.getAllBlog.length }}
                </div>
              </div>
            </li>
            <li>
              <!-- 标签数量 -->
              <div class="BQ">
                <h3>标签</h3>
                <div class="xian">————</div>
                <div class="bqNum">4</div>
              </div>
            </li>
            <li>
              <!-- 阅读数量 -->
              <div class="read">
                <h3>阅读量</h3>
                <div class="xian">————</div>
                <div class="readNum">79</div>
              </div>
            </li>
          </ul>
        </div>
        <!-- 转场github -->
        <div class="but">
          <a :href="this.$store.getters.getUserData.githubUrl" target="_blank">
            <Button type="primary" long
              ><Icon
                type="logo-github"
              />&nbsp;&nbsp;&nbsp;&nbsp;Transition</Button
            >
          </a>
        </div>
      </div>
    </li>
    <!-- 最新文章 -->
    <li>
      <div class="latest">
        <!-- 标题 -->
        <div class="latest-title">
          <Icon type="md-time" size="20" /> 最新文章
        </div>
        <br />
        <!-- <hr> -->
        <!-- 文章内容 -->
        <div class="latest-content">
          <ul
            v-for="(data, index) in this.$store.getters.getAllBlog"
            :key="index"
          >
            <li>
              <div class="latest-content-div">
                <div class="latest-content-img">
                  <img :src="data.img" alt="" />
                </div>
                <div class="latest-content-con">
                  <span class="latest-content-con-title" @click="getDesc(data.id)">{{ data.title }}</span
                  ><br />
                  <span class="latest-content-con-time">{{
                    data.createTime
                  }}</span>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </li>
  </ul>
</template>
<script>
export default {
  methods:{
    getDesc(id){
      this.$router.push({
        path: `/desc`,
        query: {
          id: id,
        },
      });
    }
  }
}
</script>
<style scoped>
@import "../main/Main";
</style>


